// React
import { useParams } from 'react-router-dom';
// Assets
import './userProfile.css';
// Shared
import { user } from '../../shared/library/local/user';
import { Link, setAppTitle } from '../../shared/components/routes/routes';


const ViewProfile = () => {
  const profileID = useParams<any>().userID;
  setAppTitle("View Profile")

  const EditProfileBtn = () => {
    return <Link to={`user/${profileID}/edit`}>
      <div id="toggle-edit-profile">
        <i className="fa-solid fa-user-pen"></i>
        <p>Edit Profile</p>
      </div>
    </Link>
  }

  return <div id="container">
    { profileID === user.UUID && <EditProfileBtn/> }
    <UserInfoContainer>
    </UserInfoContainer>
    <UserProfileImage/>
  </div>
}


const EditProfile = () => {
  const profileID = useParams<any>().userID;
  setAppTitle("Edit Profile")

  const ViewProfileBtn = () => {
    return <Link to={`user/${profileID}`}>
      <div id="toggle-edit-profile">
        <i className="fa-solid fa-eye"></i>
        <p>View Profile</p>
      </div>
    </Link>
  }

  return <div id="container">
    { profileID === user.UUID ? <ViewProfileBtn/> : window.location.href = `/user/${profileID}` }
    <UserInfoContainer edit>
    </UserInfoContainer>
      <h1>{user.FIRST_NAME}, {user.MIDDLE_NAMES}, {user.LAST_NAME}</h1>
    <UserProfileImage edit/>
  </div>
}


const UserProfileImage = ({ edit } : any) => {
  return <div id="user-display-image">
    { /* <img src={`/api/o-core/file?uuid=${user.DISPLAY_IMAGE}`} alt="user display"/> */ }
    <img src="https://upload.wikimedia.org/wikipedia/commons/7/70/Example.png" alt="user display"/>
    { edit && <div id="user-display-image-edit">
      <i className="fa-solid fa-camera"></i>
      Edit Display Image
    </div>}
  </div>
}


const UserInfoContainer = ({ edit, children } : any) => {
  return <div id="user-info-container">
    {children !== undefined ? children : <></>}
  </div>
}


export { ViewProfile, EditProfile };
